.contact{
    -ms-flex-direction: column;
        flex-direction: column;
    width: 100%;
    padding-left: 45px;
    margin:40px 0;
}

.type {
    display: inline;
    -ms-flex-align: center;
        align-items: center;
    margin: 10px 0;
    padding: 10px 20px;
    border-radius: 25px;
    background: -webkit-linear-gradient(75deg, rgba(39, 189, 169, 0.31), rgba(0, 125, 121, 0.26));
    background: -o-linear-gradient(75deg, rgba(39, 189, 169, 0.31), rgba(0, 125, 121, 0.26));
    background: linear-gradient(15deg, rgba(39, 189, 169, 0.31), rgba(0, 125, 121, 0.26));

}

.payload {
    display: inline;
    margin-left: 25px;
    border-radius: 25px;
    padding: 10px 20px;
    background: -webkit-linear-gradient(75deg, rgba(113, 62, 168, 0.31), rgba(168, 72, 115, 0.26));
    background: -o-linear-gradient(75deg, rgba(113, 62, 168, 0.31), rgba(168, 72, 115, 0.26));
    background: linear-gradient(15deg, rgba(113, 62, 168, 0.31), rgba(168, 72, 115, 0.26));
}



