.infoBlock{
    width: 100%;
    padding-left: 45px;
    margin-bottom: 20px;
    border-left: 4px solid  var(--main-color-hover);
}

.title {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}

.title a {
    width: auto;
    margin-left: 25px;
    padding: 0 15px;
    text-decoration: none;
    border-radius: 25px;
}

.title a:hover {
    background: -webkit-linear-gradient(75deg, rgba(113, 62, 168, 0.31), rgba(168, 72, 115, 0.26));
    background: -o-linear-gradient(75deg, rgba(113, 62, 168, 0.31), rgba(168, 72, 115, 0.26));
    background: linear-gradient(15deg, rgba(113, 62, 168, 0.31), rgba(168, 72, 115, 0.26));
}

.title .titleLink {
    width: auto;
    margin: 15px 5px;
    font-size: 26px;
}

.title .titleNoLink {
    width: auto;
    margin: 15px 5px;
    margin-left: 25px;
    padding: 0 15px;
    font-size: 26px;
}

.details {
    width: 100%;
    margin: 0;
    margin-top: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: start;
    border: 1px solid var(--border-color);
    border-left: 0;
    border-right: 0;
}

.detailsItem {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    width: 30%;
    margin: 15px 25px;
    margin-left: 0;
}

.detailsItem span {
    margin-left: 15px;
}

.additional{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: start;
        justify-content: start;
}

.additional button {
    border: none;
    text-align: center;
    font-size: 16px;
    margin: 25px;
    margin-left: 0;
    padding: 5px 25px;
    border-radius: 15px;
    text-decoration: none;
    color: var(--tetriary-text-color);
    background-color: var(--main-color-hover);
    -webkit-transition: 0.1s ease-in;
    -o-transition: 0.1s ease-in;
    transition: 0.1s ease-in;
}


